import BlogClient from "./BlogClient";
import { getBlogPostBySlug } from "@/lib/sanity";

interface BlogPageProps {
  params: { slug: string };
}

export default async function BlogPage({ params }: BlogPageProps) {
  const slug = params.slug;

  const blogPost = await getBlogPostBySlug(slug); // your existing hook logic in a server-friendly way

  if (!blogPost) {
    return <p>Article not found</p>; // simple fallback
  }

  return <BlogClient blogPost={blogPost} />;
}
